.process__item__wrapper {
     position: relative;

     &::before {
          position: absolute;
          content: '';
          height: 1px;
          width: 100%;  
          bottom: -120px;
          background: var(--rts-border);
     }

     &.style-2 {
          &::before {
               display: none;
          }
     }
}

.process__item__single {
  background: var(--heading-color);
  padding: 16px;
  border-radius: 10px;
  position: relative;
  z-index: 1;
  transition: var(--transition);

  @include mq($xl){
    height: 93.5%;
  }

  .number {
    color: var(--rts-neon);
  }

  .title {
    color: var(--rts-white);
    line-height: 28px;

    @media only screen and (min-width:768px) and (max-width:991px) {
      font-size: 19px;
    }
  }

  .desc {
    margin-top: 130px;
    color: var(--rts-white);
  }

  .shape {
    position: absolute;
    top: 30px;
    right: 16px;
    z-index: -1;
  }

  // Default pseudo-elements for first item (top-left & bottom-right)
  &::before,
  &::after {
    content: "";
    position: absolute;
    width: 16px;
    height: 16px;
    border: 2px solid var(--rts-neon);
    transition: var(--transition);
  }

  &::before {
    top: 10px;
    left: 10px;
    border-right: none;
    border-bottom: none;
    border-radius: 4px 0 0 0;
  }

  &::after {
    bottom: 8px;
    right: 8px;
    border-left: none;
    border-top: none;
    border-radius: 0 0 4px 0;
  }

   .corner-top-right,
    .corner-bottom-left {
      content: "";
      position: absolute;
      width: 20px;
      height: 20px;
      border: 2px solid var(--rts-neon);
      border-radius: 0 4px;
      transition: var(--transition);
    }

    .corner-top-right {
      top: 8px;
      right: 8px;
      border-left: none;
      border-bottom: none;
    }

    .corner-bottom-left {
      bottom: 8px;
      left: 8px;
      border-right: none;
      border-top: none;
     }

     &:hover {
      &::before{
        top: 5px;
        left: 5px;
      }

      &::after{
        bottom: 5px;
        right: 5px;
      }

      .corner-top-right {
        top: 5px;
        right: 5px;
      }

      .corner-bottom-left {
        bottom: 5px;
        left: 5px;
      }
      
     }
}

.rts__process__area {
  .rts__btn {
    margin-bottom: 23px;
  }
}