/* gdpr cookies */
[data-theme='dark'] {
  .gdprcookie {
    background: #1b1b1b;
  }
  .gdprcookie h2 {
    color: #fff;
  }
}
.gdprcookie {
  position: fixed;
  color: white;
  font-size: 0.8em;
  line-height: 1.5em;
  left: 1.5rem;
  bottom: 1.5rem;
  max-width: 34em;
  padding: 30px 20px;
  background: var(--rts-heading);
  z-index: 99999;
}

.gdprcookie h2 {
  color: var(--rts-white);
  font-size: 20px;
  line-height: 1.2;
  margin-bottom: 20px;
}

.gdprcookie-intro\/ p {
  margin-bottom: 20px;
  line-height: 26px;
}

.gdprcookie-buttons\/ button {
  display: inline-block;
  border: 0;
  margin-right: 12px;
  padding: 10px 15px;
  font-family: var(--inter);
  line-height: 1;
}

.gdprcookie-types\/ {
  h3 {
    color: #fff;
    font-size: 18px;
    line-height: 1.3;
    margin-bottom: 10;
  }

  ul {
    padding: 0;
    list-style: none;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5px;
    margin-bottom: 20px;
    li {
      display: flex;
      gap: 5px;
    }
  }
}

